@import '../../../imports';

.cube-header-bar {
  @extend %header-bar;

  .refreshing svg {
    animation-name: rotate;
    animation-duration: 1s;

    @keyframes rotate {
      from {
        transform: rotate(0deg);
      }

      to {
        transform: rotate(360deg);
      }
    }
  }

  .left-bar .cube-description {
    display: inline-block;
    padding: 11px 5px;
    margin-left: 5px;
    width: auto;

    svg path {
      @include css-variable(fill, background-brand-text);
    }

    &:hover {
      background-color: transparent;

      svg path {
        @include css-variable(fill, background-brand-text);
      }
    }

  }
}
