@import '../../imports';

.message-panel {
  @include pin-full;
  background: $white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.message-panel__container {
  max-width: 500px;
}

.message-panel__title {
  @include css-variable(color, brand);
  margin-bottom: 12px;
  font-size: 20px;
  text-align: center;
}

.message-panel__message {
  @include css-variable(color, text-medium);
  font-size: 14px;
}

.message-panel__children {
  margin-top: 36px;
  display: flex;
  justify-content: center;
}
