@import '../../imports';

.folder {
  .folder-header {
    background: $white;
    cursor: pointer;
    overflow: hidden;
    padding-left: 14px;
    height: $dimension-height;
    display: flex;

    &:hover {
      @include css-variable(background-color, hover);
    }

    .icon-label-container {
      display: flex;
      flex-grow: 1;
      overflow: hidden;

      .label {
        @include ellipsis;
        flex-grow: 1;
        min-width: 0;
        display: block;
        padding: 7px 0.15em;
      }

      .folder-icon {
        top: 4px;
        left: -2px;
        width: 19px;
        float: left;

        svg {
          width: 19px;

          path {
            fill: hsla(0, 0, 0, 0.35);
          }
        }
      }
    }

    .info-icon {
      @extend %hoverable-icon;
      flex-shrink: 0;
      top: 4px;
      right: 3px;
      height: 19px;
    }
  }

  .folder-items {
    padding-left: 22px;

    &.closed {
      display: none;
    }

    .row {
      &:last-child {
        margin-bottom: $searchable-folder-last-child-margin;
      }
    }
  }
}
