@import '../../imports';

.resize-handle {
  $size: 18px;

  z-index: 1;
  position: absolute;


  > .svg-icon {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 50%;
    left: 50%;

    path {
      fill: #b4b4b4;
    }
  }

  &.left, &.right {
    cursor: ew-resize;
    height: 100%;
    width: $size;

    > .svg-icon {
      transform: translate(-50%, -50%);
    }
  }

  &.left {
    transform: translate(-50%, 0);
    margin-left: -$tile-margin-h / 2;
  }

  &.right {
    transform: translate(50%, 0);
    margin-right: -$tile-margin-h / 2;
  }

  &.top, &.bottom {
    cursor: ns-resize;
    height: $size;
    width: 100%;
  }

  &.top {
    > .svg-icon {
      transform: rotate(90deg) translate(-50%, 50%);
    }
  }
}
