@import '../../imports';

.bubble-menu {
  @extend %menu-cont;
  padding: $padding-compact;

  &.mini {
    padding: $padding-mini;
  }

  .menu-header {
    @include pin-top($menu-title-bar-height);
  }

  .bubble-list-title {
    @include css-variable(color, text-lighterish);
    line-height: 25px;
  }

  ul.bubble-list {
    margin-left: -$padding-compact;
    margin-right: -$padding-compact;

    li {
      padding: 0 $padding-compact;
      line-height: 25px;
      cursor: pointer;

      &:hover {
        @include css-variable(background-color, brand-hover);
      }

      &.selected {
        @include css-variable(background-color, background-light);
      }
    }
  }

  .ok-cancel-bar {
    margin-top: 12px;

    button {
      min-width: 70px;

      &:not(:last-child) {
        margin-right: 6px;
      }
    }
  }
}
